.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #222222;
  color: #ffffff;
  padding: 1rem;
}

.navbar-logo a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.5rem;
}

.navbar-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-menu li {
  margin-left: 1rem;
}

.navbar-menu a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
}

.navbar-menu a:hover {
  color: #dddddd;
}

.navbar-menu a.active {
  color: #ffd400;
  font-weight: bold;
}
