.footer {
  background-color: #222222;
  color: #ffffff;
  opacity: 0.9;
  text-align: center;
  padding: 1rem 1rem;
  width: 100%;
  margin-top: auto;
  box-sizing: border-box;
  max-width: 100vw;
  min-height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer p {
  margin: 4px 0;
  font-size: 0.9rem;
  color: white;
}
