.illustration-page {
    display: flex;
    min-height: 100vh;
    background-color: #1a1a1a;
    justify-content: center;
    padding: 0 20px;
}

.side-panel-container {
    max-width: 370px;
    position: relative;
    flex-shrink: 0;
    margin-right: 20px;
}

.side-panel-container.mobile {
    width: 0;
}

.side-panel-container.mobile .filter-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
}

.side-panel-container.mobile .filter-popup-overlay.active {
    opacity: 1;
    visibility: visible;
}

.side-panel-container.mobile .filter-popup {
    position: relative;
    width: 90%;
    max-width: 400px;
    max-height: 85vh;
    background-color: #333;
    border-radius: 8px;
    overflow-y: auto;
    padding-top: 40px;
    transform: translateY(20px);
    transition: transform 0.3s;
    margin-top: 50px;
    scrollbar-width: thin;
    scrollbar-color: #666 #333;
}

.side-panel-container.mobile .filter-popup::-webkit-scrollbar {
    width: 8px;
}

.side-panel-container.mobile .filter-popup::-webkit-scrollbar-track {
    background: #333;
    border-radius: 4px;
}

.side-panel-container.mobile .filter-popup::-webkit-scrollbar-thumb {
    background-color: #666;
    border-radius: 4px;
    border: 2px solid #333;
}

.side-panel-container.mobile .filter-popup::-webkit-scrollbar-thumb:hover {
    background-color: #888;
}

.side-panel-container.mobile .filter-popup-overlay.active .filter-popup {
    transform: translateY(0);
}

.filter-toggle-button {
    position: fixed;
    bottom: 30px;
    right: 30px;
    z-index: 100;
    background-color: #FFFFFF;
    color: #ffd700;
    border: 6px solid #ffd700;
    padding: 16px;
    border-radius: 50%;
    cursor: pointer;
    font-size: 1.3rem;
    font-weight: bold;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
}

.filter-toggle-button:hover {
    background-color: #ffffff;
    transform: scale(1.1);
    box-shadow: 0 4px 8px rgba(255, 215, 0, 0.3);
    border-color: #ffed4a;
    color: #ffed4a;
}

.filter-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
    padding: 5px;
    z-index: 1001;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: background-color 0.2s;
}

.filter-close-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

@media (max-width: 1024px) {
    .illustration-page {
        flex-direction: column;
        align-items: center;
        padding: 0 10px;
    }
    
    .side-panel-container {
        margin-right: 0;
        margin-bottom: 20px;
    }
}